<template>
  <div class="vip">
    <image class="vip-img"
      :src="vip.pic" ></image>
    <P class="vip-text" v-if = "vip.grade > 0"> 我的等级</p>
    <P class="vip-text" v-else> 暂未开通</p>
    <p class="vip-text2" v-if = "vip.grade > 0"> VIP{{vip.grade}}</p>
    <p class="vip-text3" v-if = "vip.desc"> {{vip.desc}}</p>
   <button class="vip-button" v-if = "vip.grade > 0 && vip.grade <5"> 升级VIP</button>
   <button class="vip-button" v-if = "vip.grade == 0"> 开通VIP会员</button>
    <div class="vip-list">
      <div class="vip-item" v-for = "vipItem in vip.privilegeItems">
        <p class="vip-item-title"> {{vipItem.itemName}}</p>
        <hr class = "vip-item-title-line" />
        <div class = "vip-item" v-for = "item in vipItem.itemDetails">
          <image class="vip-item-icon"
                 :src ="item.pic" ></image>
            <div v-if = "item.desc">
              <p class="vip-item-name"> {{item.name}}</p>
              <p class="vip-item-desc"> {{item.desc}}</p>
            </div>
            <div v-else = "item.desc">
              <p class="vip-item-name-center"> {{item.name}}</p>
            </div>
            <hr class = "vip-item-line" />
          </div>
        <!--<hr class = "vip-item-title-line" />-->
      </div>
  </div>
  </div>

</template>

<script>
import { vip } from 'vx/getters'
import { getVip } from 'vx/actions'
export default {
  route: {
  activate (transition) {
    transition.next()
    this.getVip({
      uid: transition.to.query.uid
    })
  }
  },

  vuex: {
    getters: {
      vip
    },
    actions: {
      getVip
    }
  },

  components: {

  }
}
</script>

<style src="styles/views/vip"></style>



